<template>
    <div>
        <div class="card mb-4" v-for="post in listPosts" :key="post">
            <div class="card-header d-flex align-items-center">
                <img class="rounded-circle" src="https://i.imgur.com/aoKusnD.jpg" width="45">
                <div class="ms-2">
                    <p class="author">{{ post.prenom }} {{ post.nom }}</p>
                    <p class="created">{{ formatDate(post.created) }}</p>
                </div>
            </div>
            <div class="card-body">
                <p class="card-text">{{ post.content }}</p>
            </div>
            <div class="card-footer text-end">
                <button class="btn"><i class="fas fa-heart"></i></button>
                <button class="btn"><i class="fas fa-comments"></i></button>
                <button class="btn"><i class="fas fa-share"></i></button>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex';
const axios = require('axios').default;

export default {
    name: 'ListPost',
    data: () => {
        return {
            listPosts: []
        }
    }, 
    computed: {
        ...mapState(['token', 'userId']),
    },
    methods: {
        formatDate(date) {
            let newDate = new Date(date)
            let dateFormat = newDate.getDate() + '-' + newDate.getMonth() + '-' + newDate.getFullYear()
            let hours = newDate.getHours() + ':' + newDate.getMinutes() + ':' + newDate.getSeconds()
            console.log(dateFormat)
            console.log(newDate)
            console.log(hours)
        }
    },
    mounted() {
        axios.get('http://localhost:3200/api/posts')
        .then(response => {
            console.log(response.data)
            this.listPosts = response.data
        })
    },
}
</script>

<style scoped>
    p {
        margin: 0;
    }

    .card {
        width: 100%;
        border-radius: 10px;
    }

    .card .card-header {
        background: #212529;
        color: #fff;
        padding: 16px;
        border-radius: 10px 10px 0 0;
    }

    .card .card-body {
        border-radius: 0;
        padding: 16px;
    }

    .card .card-footer {
        background: #212529;
        padding: 16px;
        border-radius: 0 0 10px 10px;
    }

    .card .card-footer button:focus {
        box-shadow: none;
    }

    .card .card-footer button i {
        font-size: 20px;
        color: #fff;
        transition: all 0.5s ease-in-out;
    }

    .card .card-footer button i.fa-heart:hover {
        transition: all 0.5s ease-in-out;
        color: #dc3545;
    }

    .card .card-footer button i.fa-comments:hover {
        transition: all 0.5s ease-in-out;
        color: #FFD7D7;
    }

    .card .card-footer button i.fa-share:hover {
        transition: all 0.5s ease-in-out;
        color: #0dcaf0;
    }

    .created {
        font-size: 11px;
    }

    .author {
        font-weight: bold;
    }
</style>